
'use client';

import { useState, useEffect } from 'react';
import { notFound } from 'next/navigation';
import { Header } from '@/components/layout/Header';
import { useMockDatabase } from '@/lib/data';
import type { Volunteer, Family } from '@/types';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Loader2 } from 'lucide-react';
import AdminVolunteerPreferences from './_components/AdminVolunteerPreferences';
import AdminVolunteerAvailability from './_components/AdminVolunteerAvailability';
import AppShell from '@/components/layout/AppShell';

export default function VolunteerDetailsPage({ params }: { params: { volunteerId: string } }) {
  const db = useMockDatabase();

  const [volunteer, setVolunteer] = useState<Volunteer | null>(null);
  const [family, setFamily] = useState<Family | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const loadData = async () => {
      setIsLoading(true);
      const vol = db.getVolunteersSync().find(v => v.id === params.volunteerId);
      if (vol) {
        setVolunteer(vol);
        const fam = await db.getFamilyByVolunteerId(vol.id);
        setFamily(fam);
      }
      setIsLoading(false);
    };
    loadData();
  }, [params.volunteerId, db]);

  if (isLoading) {
    return (
      <AppShell>
        <div className="flex min-h-screen w-full flex-col bg-muted/40">
          <Header />
          <main className="flex flex-1 items-center justify-center">
            <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
          </main>
        </div>
      </AppShell>
    );
  }

  if (!volunteer) {
    return notFound();
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
          <div className="max-w-4xl mx-auto space-y-6">
            <div className="flex items-center gap-4">
              <Avatar className="h-16 w-16">
                <AvatarImage src={`https://placehold.co/64x64.png`} data-ai-hint="person" />
                <AvatarFallback>{volunteer.name.charAt(0)}</AvatarFallback>
              </Avatar>
              <div>
                <h1 className="text-3xl font-bold font-headline tracking-tight">{volunteer.name}</h1>
                <p className="text-muted-foreground">{volunteer.email}</p>
              </div>
            </div>
            <Tabs defaultValue="preferences">
              <TabsList>
                <TabsTrigger value="preferences">Preferences</TabsTrigger>
                <TabsTrigger value="availability">Availability</TabsTrigger>
              </TabsList>
              <TabsContent value="preferences" className="mt-4">
                  <AdminVolunteerPreferences volunteer={volunteer} db={db} />
              </TabsContent>
              <TabsContent value="availability" className="mt-4">
                  <AdminVolunteerAvailability volunteer={volunteer} family={family} db={db} />
              </TabsContent>
            </Tabs>
          </div>
        </main>
      </div>
    </AppShell>
  );
}
